import React from "react";
import styles from "./status.scss";


export default function status(props) {
  /**
    thing 何物
    status 属性
 */
  const { thing } = props;
  const status = ["att", "matt", "str", "dex", "int", "luk"];

  // 绘制属性
  const renderStatus = (status) => {
    if (thing[status].min !== null && thing[status].max !== null) {
      if (thing[status].min !== thing[status].max) {
        return (
          <span>
            {status}: {thing[status].min}~{thing[status].max}
          </span>
        );
      } else {
        return (
          <span>
            {status}: {thing[status].min}
          </span>
        );
      }
    }
  };

  // 绘制结果
  // const renderResult = status;

  return (
    <div className={styles.status}>
      <img className={styles.icon} src={thing.icon} alt="" />
      {status.map((status, key) => {
        return <div key={key}>{renderStatus(status)}</div>;
      })}
    </div>
  );
}
