import React from "react";
import styles from "./craft.scss";

import itemCraft from "../../utils/itemCraft";
import Status from "./status";
import Result from "./result";
function Craft(props) {
  return (
    <div>
      {itemCraft[props.job] &&
        itemCraft[props.job].map((item, key) => {
          return (
            <div className={styles.craft} style={{}} key={key}>
              <div className={styles.item} id="x">
                <img className={styles.icon} src={item.icon} alt="" />
              </div>
              <div className={styles.craftContent}>
                <h3>
                  <span>Newborn #1</span>
                </h3>
                <div className={styles.itemRaw} id="x">
                  <Status thing={item.raw}></Status>
                </div>
              </div>
              <div className={styles.craftContent}>
                <h3>
                  <span>Addition #2</span>
                </h3>
                <div className={styles.itemAdd} id="x">
                  <Status thing={item.add1}></Status>
                  <Status thing={item.add2}></Status>
                  <Status thing={item.add3}></Status>
                  <Status thing={item.add4}></Status>
                  <Result
                    things={[
                      item.raw,
                      item.add1,
                      item.add2,
                      item.add3,
                      item.add4,
                    ]}
                  ></Result>
                </div>
                Comment
              </div>
              <div className={styles.craftContent}>
                <h3>
                  <span>Level Up X5 #3</span>
                </h3>
                <div className={styles.itemLevel} id="x">
                  <Status thing={item.level}></Status>
                  <Result
                    things={[
                      item.raw,
                      item.add1,
                      item.add2,
                      item.add3,
                      item.add4,
                      item.level,
                    ]}
                  ></Result>
                </div>
              </div>
              <div className={styles.craftContent}>
                <h3>
                  <span>Scroll It #4</span>
                </h3>
                <div className={styles.itemScroll} id="x">
                  <Status thing={item.scroll}></Status>
                  <Result
                    things={[
                      item.raw,
                      item.add1,
                      item.add2,
                      item.add3,
                      item.add4,
                      item.level,
                      item.scroll,
                    ]}
                  ></Result>
                </div>
              </div>
              <div className={styles.craftContent}>
                <h3>
                  <span>Score #5</span>
                </h3>
                <div className={styles.itemScore} id="x">
                  <Status thing={item.raw}></Status>
                </div>
              </div>
            </div>
          );
        })}
    </div>
  );
}

export default Craft;
